/* global Office */

Office.onReady(() => {
  // Required for command functions.
});

// Command function invoked by ExecuteFunction
function prefillTo(event) {
  const item = Office.context.mailbox.item;

  // Safety: this should only run in compose
  // (You already scoped the manifest, but extra guard doesn't hurt)
  if (!item || !item.to || typeof item.to.setAsync !== "function") {
    console.warn("Not in compose or 'to' field not available.");
    event.completed();
    return;
  }

  const recipients = [
    {
      displayName: "Ares Safety Coalition",
      emailAddress: "AresSafetyCoalition-SM@aresmgmt.com",
    },
    // Add more if you want:
    // { displayName: "Stanley Li", emailAddress: "sli@aresmgmt.com" },
  ];

  item.to.setAsync(recipients, (asyncResult) => {
    if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
      // Optional: also set subject/body here if you want
      console.log("To recipients set.");
    } else {
      console.error("Failed to set To:", asyncResult.error);
    }
    event.completed(); // MUST call for command functions
  });
}

// IMPORTANT: expose the function for Outlook command runtime
Office.actions.associate("prefillTo", prefillTo);